
.App-header {
  background: conic-gradient(from var(--angle) at 35% 50%, #D4E2E8 0deg, #1B3540 360deg);
  animation: background-animation infinite 10s linear;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.main-text{
  font-family: 'Inter';
  font-style: normal;
  font-weight: 600;
  text-align: right;
  margin: auto 15vw auto auto;
  width: 50vw;
}
.main-text>p:first-child {
  font-size: 3pc;
  color: #1B3540;
}
.main-text>p:nth-child(2) {
  color: #1B3540;
  font-weight: 300;
  font-size: 1.5pc;
}
.contact {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 600;
  font-size: 1.3pc;
  color: #1B3540;
  margin-right: 5vw;
  text-align: right;
}
a {
  color: #1B3540;
  text-decoration: none;
}
@property --angle {
  syntax: '<angle>';
  inherits: false;
  initial-value: 245deg;
}
@keyframes background-animation {
  from{
    --angle:0deg;
  }
  to {
    --angle:360deg;
  }
}


@media screen and (max-width: 480px){
  .main-text {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 600;
    text-align: center;
    margin: auto auto auto auto;
    width: auto;
  }
  .main-text>p:first-child {
    font-size: 2pc;
  }
}